@-webkit-keyframes spin-right {
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@keyframes spin-right {
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

.button-reversed {
  border: black dashed 2px;
  background-color: transparent;
  color: black;
  font-family: 'Space Mono', monospace;
  font-size: 1em;
  padding: .5em;
  margin: .5em;
}

.buttonReversed:hover {
  border: black 2px outset;
  cursor: pointer;
}

button {
  border: #ecf0f1 dashed 2px;
  background-color: transparent;
  color: #ecf0f1;
  font-family: 'Space Mono', monospace;
  font-size: 1em;
  padding: .5em;
}

#button-style {
  border: #ecf0f1 dashed 2px;
  background-color: transparent;
  text-decoration: none;
  color: #ecf0f1;
  font-family: 'Space Mono', monospace;
  font-size: 1em;
  padding: .5em;
  margin: .5em;
}

button:hover {
  border: #ecf0f1 2px outset;
  cursor: pointer;
}

#button-style:hover {
  border: #ecf0f1 2px outset;
  cursor: pointer;
}

button:focus {
  outline: 0;
}

